
.about-h4 {
    float: left;
    margin: 30px;
    margin-left: 5px;
    font-weight: 200;
}

.h4-border {
    margin-left: 5px;
    margin-top: 75px;
    margin-right: 300px;
    margin-bottom: 10px;
    width: 50px;
    border-bottom: 2px solid #e67e22;;
}

.about-container {
    margin-bottom: auto;
    height: 80vh;
    width: 100vw;
    background-color: rgb(236, 232, 232);
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
    align-content: center;
    overflow-y: auto;
}

::-webkit-scrollbar {
    width: 20px;
  }

.doc-containers {
    overflow-y:auto;
    padding: 10px;
    padding-left: 25px;
    margin: 15px;
    margin-top: auto;
    margin-bottom: auto;
    height: 500px;
    width: 400px;
    background-color: white;
    border-radius: 5%;
    box-shadow: 1px 1px#d1d1d1;
    color: #555;
    text-align: left;
}

